import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Tooltip, Col, Spin, Modal, Icon } from "antd";

import ModalAddRaptorConnection from "./modalAddRaptorConnection";
import ModalUpdateRaptorConnection from "./modalUpdateRaptorConnection";

import Popconfirms from "../../../../components/feedback/popconfirm";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/setting/appConfig/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";

import getLanguage from "../../../App/MyLanguange";

class RaptorConnection extends Component {
  state = {
    visibleAdd: false,
    visibleUpdate: false,
    raptor_connection_data: null
  };

  componentDidUpdate(prevProps) {
    if (
      prevProps.appConfig.loadingConnection &&
      !this.props.appConfig.loadingConnection
    ) {
      Modal.destroyAll();
    }
  }

  handleModalAdd = () => {
    this.setState({
      visibleAdd: !this.state.visibleAdd
    });
  };

  handleModalUpdate = row => {
    this.state.visibleUpdate
      ? this.setState({ visibleUpdate: false, raptor_connection_data: {} })
      : this.setState({ visibleUpdate: true, raptor_connection_data: row });
  };

  // handleDelete = (id) => {
  //   let values = {};
  //   values.id_raptor_connection = id;
  //   values.is_active = 0;
  //   this.props.handleSubmitUpdateRaptorConnection(values)
  // }

  checkRaptor = row => {
    const { checkRaptorConnection } = this.props;

    let data = {
      ip_address: row.ip_address,
      port: row.db_port,
      db_name: row.db_name,
      username: row.db_username,
      password: row.db_password
    };

    checkRaptorConnection(data);

    Modal.info({
      title: getLanguage({ id: "checkingRaptorDatabase" }),
      content: getLanguage({ id: "pleaseWait" }),
      okButtonProps: {
        style: {
          display: "none"
        }
      },
      centered: true,
      keyboard: false,
      icon: <Icon type="loading" />
    });
  };

  handleRecord = (action, id) => {
    let values = {};
    values.id_raptor_connection = id;

    if (action === "activate") {
      values.is_active = 1;
    } else {
      values.is_active = 0;
    }

    this.props.handleSubmitUpdateRaptorConnection(values);
  };

  // Definisi kolom2 serta field2 API yang dipakai tabel
  listAction = {
    title: "",
    dataIndex: "id_raptor_connection",
    rowKey: "id_raptor_connection",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <React.Fragment>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Button
            size="small"
            className="invoiceEdtBtn"
            onClick={() => this.handleModalUpdate(row)}
          >
            <i className="ion-android-create" />
          </Button>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={
              <IntlMessages id="raptorConnections.popconfirm.deactivate" />
            }
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText="Yes"
            cancelText="No"
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="raptorConnections.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText="Yes"
            cancelText="No"
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
        <Tooltip
          placement="topRight"
          title={<IntlMessages id="tooltip.checkConnection" />}
        >
          <Button
            size="small"
            className="invoiceRstBtn"
            onClick={this.checkRaptor.bind(this, row)}
          >
            <i className="ion-arrow-swap" />
          </Button>
        </Tooltip>
      </React.Fragment>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "ipAddress" }),
        dataIndex: "ip_address",
        rowKey: "ip_address",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "port" }),
        dataIndex: "db_port",
        rowKey: "db_port",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "dbName" }),
        dataIndex: "db_name",
        rowKey: "db_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "username" }),
        dataIndex: "db_username",
        rowKey: "db_username",
        width: "20%",
        render: text => <span>{text}</span>
      }
    ];

    // Data setup
    const { raptor_connections, loading } = this.props.appConfig;
    //console.log(supplier)
    //console.log(this.props.appConfig); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update App Config") && columns.length === 5) {
      columns.push(this.listAction);
    }

    Object.keys(raptor_connections).map((raptor_connection, index) => {
      return dataSource.push({
        ...raptor_connections[raptor_connection],
        key: raptor_connection
      });
    });

    return (
      <>
        <ModalAddRaptorConnection
          visible={this.state.visibleAdd}
          handleModal={this.handleModalAdd}
          submit={this.props.handleSubmitAddRaptorConnection}
          appConfig={this.props.appConfig}
        />
        <ModalUpdateRaptorConnection
          visible={this.state.visibleUpdate}
          raptor_connection_data={this.state.raptor_connection_data}
          handleModal={this.handleModalUpdate}
          submit={this.props.handleSubmitUpdateRaptorConnection}
          appConfig={this.props.appConfig}
        />

        <div style={{ marginRight: "20px", float: "right" }}>
          <Button type="primary" onClick={this.handleModalAdd.bind(this)}>
            <IntlMessages id="button.addRaptorConnection" />
          </Button>
        </div>

        {this.props.appConfig.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (
          <CardWrapper>
            <Col
              span={24}
              style={{ marginTop: "20px", padding: "0 20px 0 20px" }}
            >
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    // size="small"
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    pagination={true}
                  />
                </Scrollbars>
              </div>
            </Col>
          </CardWrapper>
        )}
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    appConfig: state.appConfig,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(RaptorConnection);
