import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Row, Col, InputNumber, Spin, Input } from "antd";
import actions from "../../../../redux/setting/appConfig/actions";

//const { Option } = Select;
// const { TextArea } = Input;
const InputGroup = Input.Group;

class AppConfigSyncForm extends Component {

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateAppConfig } = this.props;

        let values = [
          {
            app_key: "sync_limit",
            app_value: value.sync_limit
          },
          {
            app_key: "log_limit",
            app_value: value.log_limit
          },
          {
            app_key: "read_only_limit",
            app_value: value.read_only_limit
          },
          {
            app_key: "sync_limit_get_data",
            app_value: value.sync_limit_get_data
          },
          
        ]
        // console.log(values);
        updateAppConfig(values);
      }
    });
  };


  render() {
   // console.log(this.props.appConfig);
    const { getFieldDecorator } = this.props.form;
    const { appConfig } = this.props;
    const { loading, loadingSubmit } = appConfig;
    const app_configs = appConfig.app_configs ? appConfig.app_configs : [];
    // console.log(app_configs);
    // const used = data.is_used;
    // const error = appConfig.errorField ? appConfig.errorField : [];
    let sync_limit=null;
    let log_limit=null;
    let read_only_limit=null;
    let sync_limit_get_data=null;
    

    app_configs.map((data) => {
      if(data.app_key === "sync_limit"){
        sync_limit = data.app_value
      }
      if(data.app_key === "log_limit"){
        log_limit = data.app_value
      }
      if(data.app_key === "read_only_limit"){
        read_only_limit = data.app_value
      }
      if(data.app_key === "sync_limit_get_data"){
        sync_limit_get_data = data.app_value
      }
    })

    // console.log(search_limit);


    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <div>
        {this.props.appConfig.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (

      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="syncLimit" />}
                >
                  {getFieldDecorator("sync_limit", {
                    initialValue: sync_limit,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={1}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="logLimit" />}
                >
                  {getFieldDecorator("log_limit", {
                    initialValue: log_limit,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={1}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="readOnlyLimit" />}
                >
                  {getFieldDecorator("read_only_limit", {
                    initialValue: read_only_limit,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={1}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="syncLimitGetData" />}
                >
                  {getFieldDecorator("sync_limit_get_data", {
                    initialValue: sync_limit_get_data,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={1}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
            
          </div>
        </div>

        <Row>
          <Col>
          <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
          </Col>
        </Row>

        
      </Form>
      )}
      </div>
    );
  }
}

const WrappedAppConfigSyncForm = Form.create({
  name: "app_config_sync"
})(AppConfigSyncForm);

//export default WrappedAppConfigSyncForm;

const mapStateToProps = state => {
  return { appConfig: state.appConfig };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedAppConfigSyncForm);
