import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Row, Col, Input, Spin } from "antd";
import actions from "../../../../redux/setting/appConfig/actions";

//const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class AppConfigSyncForm extends Component {

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateAppConfig } = this.props;

        let values = [
          // {
          //   app_key: "main_key",
          //   app_value: value.main_key
          // },
          // {
          //   app_key: "secret_iv",
          //   app_value: value.secret_iv
          // },
          {
            app_key: "license_path",
            app_value: value.license_path
          },
          {
            app_key: "db_version",
            app_value: value.db_version
          },
          
        ]
        // console.log(values);
        updateAppConfig(values);
      }
    });
  };


  render() {
   // console.log(this.props.appConfig);
    const { getFieldDecorator } = this.props.form;
    const { appConfig } = this.props;
    const { loading, loadingSubmit } = appConfig;
    const app_configs = appConfig.app_configs ? appConfig.app_configs : [];
    const roles = this.props.auth.user ? this.props.auth.user.roles : []

    let is_superadmin=0;
    roles.map((data)=> {
      if(data.is_superadmin===1){
        is_superadmin = 1;
      }
    })
    // console.log(app_configs);
    // const used = data.is_used;
    // const error = appConfig.errorField ? appConfig.errorField : [];
    // let main_key=null;
    // let secret_iv=null;
    let license_path=null;
    let db_version=null;
    

    app_configs.map((data) => {
      // if(data.app_key === "main_key"){
      //   main_key = data.app_value
      // }
      // if(data.app_key === "secret_iv"){
      //   secret_iv = data.app_value
      // }
      if(data.app_key === "license_path"){
        license_path = data.app_value
      }
      if(data.app_key === "db_version"){
        db_version = data.app_value
      }
    })

    // console.log(search_limit);


    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <div>
        {this.props.appConfig.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (

      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            {/* <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="mainKey" />}
            >
              {getFieldDecorator("main_key", {
                initialValue: main_key,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Input
                  disabled={is_superadmin ? false : true}
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="secretIv" />}
            >
              {getFieldDecorator("secret_iv", {
                initialValue: secret_iv,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Input
                  disabled={is_superadmin ? false : true}
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>
            
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}> */}
          
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="licensePath" />}
            >
              {getFieldDecorator("license_path", {
                initialValue: license_path,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Input
                  disabled={is_superadmin ? false : true}
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>
            
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="dbVersion" />}
            >
              {getFieldDecorator("db_version", {
                initialValue: db_version,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Input
                  disabled={true}
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>
            
          </div>
        </div>

        {
          is_superadmin 
          ?
            (
              <Row>
                <Col>
                  <Form.Item {...tailFormItemLayout}>
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{ marginTop: "20px", marginLeft: "50px" }}
                      loading={loading || loadingSubmit}
                    >
                      <IntlMessages id="button.submit" />
                    </Button>
                  </Form.Item>
                </Col>
              </Row>
            )
          :
            null 

        }

        
      </Form>
      )}
      </div>
    );
  }
}

const WrappedAppConfigSyncForm = Form.create({
  name: "app_config_sync"
})(AppConfigSyncForm);

//export default WrappedAppConfigSyncForm;

const mapStateToProps = state => {
  return { 
    appConfig: state.appConfig, 
    auth: state.auth 
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedAppConfigSyncForm);
