import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Row, Col, Select, Spin, InputNumber } from "antd";
import actions from "../../../../redux/setting/appConfig/actions";

const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class AppConfigDefaultForm extends Component {

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateAppConfig } = this.props;

        let values = [
          {
            app_key: "default_id_customer_type",
            app_value: value.default_id_customer_type
          },
          {
            app_key: "search_limit",
            app_value: value.search_limit
          },
          {
            app_key: "default_pagination",
            app_value: value.default_pagination
          },
          {
            app_key: "session_limit",
            app_value: value.session_limit*60000
          },
          {
            app_key: "default_id_country",
            app_value: value.default_id_country
          },
          {
            app_key: "default_id_tax_item",
            app_value: value.default_id_tax_item
          },
          
          
        ]
        // console.log(values);
        updateAppConfig(values);
      }
    });
  };


  render() {
   // console.log(this.props.appConfig);
    const { getFieldDecorator } = this.props.form;
    const { appConfig } = this.props;
    const { loading, loadingSubmit } = appConfig;
    const app_configs = appConfig.app_configs ? appConfig.app_configs : [];
    // console.log(app_configs);
    // const used = data.is_used;
    // const error = appConfig.errorField ? appConfig.errorField : [];
    let default_id_customer_type=null;
    let search_limit=null;
    let default_pagination=null;
    let default_id_country=null;
    let default_id_tax_item=null;
    let session_limit=null;
    

    app_configs.map((data) => {
      if(data.app_key === "default_id_customer_type"){
        default_id_customer_type = data.app_value
      }
      if(data.app_key === "search_limit"){
        search_limit = data.app_value
      }
      if(data.app_key === "default_pagination"){
        default_pagination = data.app_value
      }
      if(data.app_key === "session_limit"){
        session_limit = parseInt(data.app_value)/60000
      }
      if(data.app_key === "default_id_country"){
        default_id_country = data.app_value
      }
      if(data.app_key === "default_id_tax_item"){
        default_id_tax_item = data.app_value
      }
    })

    // console.log(search_limit);


    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <div>
        {this.props.appConfig.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (

      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="defaultCountry" />}
            >
              {getFieldDecorator("default_id_country", {
                initialValue: default_id_country,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  style={{ width: "95%" }}
                  loading={this.props.appConfig.loading}
                  showSearch
                  optionFilterProp="children"
                  // disabled={used ? true : false}
                >
                  {this.props.appConfig.countries.map((data, key) => (
                    <Option key={key} value={data.id_country}>
                      {data.country_name}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="defaultTaxItem" />}
            >
              {getFieldDecorator("default_id_tax_item", {
                initialValue: default_id_tax_item,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  style={{ width: "95%" }}
                  loading={this.props.appConfig.loading}
                  showSearch
                  optionFilterProp="children"
                  // disabled={used ? true : false}
                >
                  {this.props.appConfig.taxes.map((data, key) => (
                    <Option key={key} value={data.id_tax}>
                      {data.tax_name}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="defaultCustomerType" />}
            >
              {getFieldDecorator("default_id_customer_type", {
                initialValue: default_id_customer_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.appConfig.loading}
                  showSearch
                  optionFilterProp="children"
                  style={{ width: "95%" }}

                  // disabled={used ? true : false}
                >
                  {this.props.appConfig.customer_types.map((data, key) => (
                    <Option key={key} value={data.id_customer_type}>
                      {data.customer_type_code + " (" + data.customer_type_name  +")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="autoLogout" />}
            >
              {getFieldDecorator("session_limit", {
                initialValue: session_limit,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <InputNumber
                  formatter={value =>
                    `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  min={10}
                  max={60}
                  precision={0}
                  style={{ width: "95%" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="searchLimit" />}
            >
              {getFieldDecorator("search_limit", {
                initialValue: search_limit,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  style={{ width: "95%" }}
                  optionFilterProp="children"
                  filterOption={true}

                >
                  <Option key={1} value={10}>
                    10
                  </Option>
                  <Option key={2} value={25}>
                    25
                  </Option>
                  <Option key={3} value={50}>
                    50
                  </Option>
                  <Option key={4} value={100}>
                    100
                  </Option>
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="defaultPagination" />}
            >
              {getFieldDecorator("default_pagination", {
                initialValue: default_pagination,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  style={{ width: "95%" }}
                  optionFilterProp="children"
                  filterOption={true}

                >
                  <Option key={1} value={10}>
                    10
                  </Option>
                  <Option key={2} value={25}>
                    25
                  </Option>
                  <Option key={3} value={50}>
                    50
                  </Option>
                  <Option key={4} value={100}>
                    100
                  </Option>
                </Select>
              )}
            </Form.Item>
            
            
          </div>
        </div>

        <Row>
          <Col>
          <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
          </Col>
        </Row>

        
      </Form>
      )}
      </div>
    );
  }
}

const WrappedAppConfigDefaultForm = Form.create({
  name: "app_config_default"
})(AppConfigDefaultForm);

//export default WrappedAppConfigDefaultForm;

const mapStateToProps = state => {
  return { appConfig: state.appConfig };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedAppConfigDefaultForm);
