import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import DefaultForm from "./updateDefaultForm";
import SyncForm from "./updateSyncForm";
import InfoForm from "./updateInfoForm";
import RoundingForm from "./updateRoundingForm";
import IntegrationForm from "./updateIntegrationForm";
import RaptorConnection from "./indexRaptorConnection";

import { Tabs, Spin } from "antd";

import notification from "../../../../components/notification";
import Button from "../../../../components/uielements/button";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/setting/appConfig/actions";
// import { auth } from "../../../../helpers/helper";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/menus";

class UpdateAppConfig extends Component {
  state = {};

  componentDidMount = () => {
    const { getAppConfigs } = this.props;
    getAppConfigs();
  };

  handleSubmitAddRaptorConnection = value => {
    const { storeRaptorConnection } = this.props;
    storeRaptorConnection(value)
  }

  handleSubmitUpdateRaptorConnection = value => {
    const { updateRaptorConnection } = this.props;
    updateRaptorConnection(value.id_raptor_connection, value);
  }

  render() {
    const roles = this.props.auth.user ? this.props.auth.user.roles : [];
    const { appConfig } = this.props;
    const app_configs = appConfig.app_configs ? appConfig.app_configs : [];

    let raptor_module = 0;

    app_configs.map((data) => {
      if (data.app_key === "raptor_module") {
        raptor_module = parseFloat(data.app_value)
      }
    })

    let is_superadmin = 0;
    roles.map(data => {
      if (data.is_superadmin === 1) {
        is_superadmin = 1;
      } else if (data.is_superadmin === 2) {
        is_superadmin = 2;
      }
    });

    //Display error notif
    if (this.props.appConfig.error) {
      notification("error", this.props.appConfig.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.appConfig.success) {
      notification("success", this.props.appConfig.success);
      this.props.clearSuccess();
    }
    // console.log(this.props.appConfig);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="appConfigs.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn" style={{ margin: 0 }}>
            {/* <h3 className="isoCode">
              <IntlMessages id="appConfigs.update.title" />
            </h3> */}
            {/* <Button onClick={ () =>{this.props.history.replace(redirectTo)}}>
              <IntlMessages id="button.back" />
            </Button> */}
          </div>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="appConfigs.tab.default" />} key="1">
              <DefaultForm auth={this.props.auth} />
            </TabPane>
            {is_superadmin === 1 || is_superadmin === 2 ? (
              <TabPane tab={<IntlMessages id="appConfigs.tab.sync" />} key="2">
                <SyncForm auth={this.props.auth} />
              </TabPane>
            ) : null}
            <TabPane
              tab={<IntlMessages id="appConfigs.tab.rounding" />}
              key="3"
            >
              <RoundingForm auth={this.props.auth} />
            </TabPane>
            <TabPane tab={<IntlMessages id="appConfigs.tab.info" />} key="4">
              <InfoForm auth={this.props.auth} />
            </TabPane>
            <TabPane tab={<IntlMessages id="appConfigs.tab.integration" />} key="5">
              <IntegrationForm auth={this.props.auth} />
            </TabPane>
            {
              raptor_module === 1
                ?
                (
                  <TabPane tab={<IntlMessages id="appConfigs.tab.raptor" />} key="6">
                    {this.props.appConfig.loading ? (
                      <div className="spinStyle">
                        <Spin size="large" />
                      </div>
                    ) : (
                        <RaptorConnection
                          auth={this.props.auth}
                          handleSubmitAddRaptorConnection={this.handleSubmitAddRaptorConnection}
                          handleSubmitUpdateRaptorConnection={this.handleSubmitUpdateRaptorConnection}
                        />
                      )
                    }
                  </TabPane>
                )
                : null
            }

          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    appConfig: state.appConfig,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateAppConfig);
