const actions = {
  getAppConfigs: () => ({
    type: "GET_APP_CONFIGS_REQUEST",
  }),
  updateAppConfig: (data) => ({
    type: "UPDATE_APP_CONFIG_REQUEST",
    payload: {
      body: data
    }
  }),
  checkRaptorConnection: (data) => ({
    type: "CHECK_RAPTOR_CONNECTION_REQUEST",
    payload: {
      body: data
    }
  }),
  storeRaptorConnection: data => ({
    type: "STORE_RAPTOR_CONNECTION_REQUEST",
    payload: { body: data }
  }),
  updateRaptorConnection: (id_raptor_connection, data) => ({
    type: "UPDATE_RAPTOR_CONNECTION_REQUEST",
    payload: {
      id_raptor_connection,
      body: data
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
