import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Row,
  Col,
  Spin,
  Radio,
  Input,
  DatePicker,
  TimePicker
} from "antd";
import actions from "../../../../redux/setting/appConfig/actions";

//const { Option } = Select;
// const { TextArea } = Input;
const InputGroup = Input.Group;

class AppConfigSyncForm extends Component {
  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateAppConfig } = this.props;

        let values = [
          {
            app_key: "raptor_module",
            app_value: value.raptor_module
          },
          {
            app_key: "raptor_sync_type",
            app_value: value.raptor_sync_type
          },
          {
            app_key: "raptor_initial_date",
            app_value: value.raptor_initial_date.format("YYYY-MM-DD")
          },
          {
            app_key: "raptor_initial_time",
            app_value: value.raptor_initial_time.format("HH:mm:ss")
          }
        ];
        // console.log(values);
        updateAppConfig(values);
      }
    });
  };

  render() {
    // console.log(this.props.appConfig);
    const { getFieldDecorator } = this.props.form;
    const { appConfig } = this.props;
    const { loading, loadingSubmit } = appConfig;
    const app_configs = appConfig.app_configs ? appConfig.app_configs : [];

    let raptor_module = null;
    let raptor_sync_type = null;
    let raptor_initial_date = null;
    let raptor_initial_time = null;

    app_configs.map(data => {
      if (data.app_key === "raptor_module") {
        raptor_module = parseFloat(data.app_value);
      }
      if (data.app_key === "raptor_sync_type") {
        raptor_sync_type = parseFloat(data.app_value);
      }
      if (data.app_key === "raptor_initial_date") {
        raptor_initial_date = moment(data.app_value, "YYYY-MM-DD");
      }
      if (data.app_key === "raptor_initial_time") {
        raptor_initial_time = moment(data.app_value, "HH:mm:ss");
      }
    });

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "50%"
    };

    const radioGroupStyle2 = {
      display: "flex",
      width: "100%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <div>
        {this.props.appConfig.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (
          <Form layout="vertical" onSubmit={this.handleSubmit}>
            <div className="isoDetailDataFlex" style={{ padding: "0" }}>
              <div
                className="isoColumn-2 isoFormGroupComp"
                style={{ padding: "0 15px 0 0" }}
              >
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="raptorModule" />}
                >
                  {getFieldDecorator("raptor_module", {
                    initialValue: raptor_module,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                      <Radio.Button style={radioButtonStyle} value={1}>
                        <IntlMessages id="active" />
                      </Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={0}>
                        <IntlMessages id="inactive" />
                      </Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>

                <Form.Item
                  {...formItemLayout}
                  style={{ display: "none" }}
                  label={<IntlMessages id="raptorSyncType" />}
                >
                  {getFieldDecorator("raptor_sync_type", {
                    initialValue: raptor_sync_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Radio.Group buttonStyle="solid" style={radioGroupStyle2}>
                      <Radio.Button style={radioButtonStyle} value={1}>
                        <IntlMessages id="invoiceDetail" />
                      </Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={2}>
                        <IntlMessages id="invoiceSummary" />
                      </Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>

                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="raptorInitialDate" />}
                >
                  {getFieldDecorator("raptor_initial_date", {
                    initialValue: raptor_initial_date,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "50%" }}
                      format="DD MMMM YYYY"
                      allowClear={false}
                    />
                  )}
                </Form.Item>

                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="raptorInitialTime" />}
                >
                  {getFieldDecorator("raptor_initial_time", {
                    initialValue: raptor_initial_time,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <TimePicker
                      style={{ width: "50%" }}
                      format="HH:mm:ss"
                      placeholder="Select time"
                      allowClear={false}
                    />
                  )}
                </Form.Item>
              </div>
            </div>

            <Row style={{ marginTop: "20px", marginLeft: "50px" }}>
              <InputGroup compact>
                <Col>
                  <Form.Item {...tailFormItemLayout}>
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{ marginRight: "15px" }}
                      loading={loading || loadingSubmit}
                    >
                      <IntlMessages id="button.submit" />
                    </Button>
                  </Form.Item>
                </Col>
              </InputGroup>
            </Row>
          </Form>
        )}
      </div>
    );
  }
}

const WrappedAppConfigSyncForm = Form.create({
  name: "app_config_sync"
})(AppConfigSyncForm);

//export default WrappedAppConfigSyncForm;

const mapStateToProps = state => {
  return { appConfig: state.appConfig };
};

export default compose(connect(mapStateToProps, actions))(
  WrappedAppConfigSyncForm
);
