import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Row, Col, InputNumber, Spin, Radio } from "antd";
import actions from "../../../../redux/setting/appConfig/actions";

//const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class AppConfigSyncForm extends Component {

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateAppConfig } = this.props;

        let values = [
          {
            app_key: "pos_rounding",
            app_value: value.pos_rounding
          },
          {
            app_key: "rounding_to",
            app_value: value.rounding_to
          },
          {
            app_key: "rounding_type",
            app_value: value.rounding_type
          },

        ]
        // console.log(values);
        updateAppConfig(values);
      }
    });
  };


  render() {
    // console.log(this.props.appConfig);
    const { getFieldDecorator } = this.props.form;
    const { appConfig } = this.props;
    const { loading, loadingSubmit } = appConfig;
    const app_configs = appConfig.app_configs ? appConfig.app_configs : [];
    // console.log(app_configs);
    // const used = data.is_used;
    // const error = appConfig.errorField ? appConfig.errorField : [];
    let pos_rounding = null;
    let rounding_to = null;
    let rounding_type = null;


    app_configs.map((data) => {
      if (data.app_key === "pos_rounding") {
        pos_rounding = parseFloat(data.app_value)
      }
      if (data.app_key === "rounding_to") {
        rounding_to = parseFloat(data.app_value)
      }
      if (data.app_key === "rounding_type") {
        rounding_type = parseFloat(data.app_value)
      }
    })

    // console.log(search_limit);


    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "50%"
    }

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    }

    return (
      <div>
        {this.props.appConfig.loading ? (
          <div className="spinStyle">
            <Spin size="large" />
          </div>
        ) : (

            <Form layout="vertical" onSubmit={this.handleSubmit}>
              <div className="isoDetailDataFlex" style={{ padding: "0" }}>
                <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="posRounding" />}
                  >
                    {getFieldDecorator("pos_rounding", {
                      initialValue: pos_rounding,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Radio.Group
                        buttonStyle="solid"
                        style={radioGroupStyle}
                      >
                        <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="active" /></Radio.Button>
                        <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="inactive" /></Radio.Button>
                      </Radio.Group>
                    )}
                  </Form.Item>


                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="roundingTo" />}
                  >
                    {getFieldDecorator("rounding_to", {
                      initialValue: rounding_to,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={1}
                        style={{ width: "50%" }}
                      />
                    )}
                  </Form.Item>

                </div>
              </div>
              <Row>
                <Col span={14}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="roundingType" />}
                  >
                    {getFieldDecorator("rounding_type", {
                      initialValue: rounding_type,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Radio.Group
                        buttonStyle="solid"
                        style={{ display: "flex", width: "100%" }}
                      >
                        <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="up" /></Radio.Button>
                        <Radio.Button style={radioButtonStyle} value={2}><IntlMessages id="down" /></Radio.Button>
                        <Radio.Button style={radioButtonStyle} value={3}><IntlMessages id="halfUp" /></Radio.Button>
                        <Radio.Button style={radioButtonStyle} value={4}><IntlMessages id="halfDown" /></Radio.Button>
                      </Radio.Group>
                    )}
                  </Form.Item>
                </Col>

              </Row>
              <Row>
                <Col>
                  <Form.Item {...tailFormItemLayout}>
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{ marginTop: "20px", marginLeft: "50px" }}
                      loading={loading || loadingSubmit}
                    >
                      <IntlMessages id="button.submit" />
                    </Button>
                  </Form.Item>
                </Col>
              </Row>


            </Form>
          )}
      </div>
    );
  }
}

const WrappedAppConfigSyncForm = Form.create({
  name: "app_config_sync"
})(AppConfigSyncForm);

//export default WrappedAppConfigSyncForm;

const mapStateToProps = state => {
  return { appConfig: state.appConfig };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedAppConfigSyncForm);
