import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import { Form, Input, InputNumber, Modal, Col, Select } from "antd";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const InputGroup = Input.Group;

class ModalUpdateRaptorConnection extends Component {
  componentDidUpdate(prevProps) {
    const { raptor_connection_data } = this.props;
    const { setFieldsValue } = this.props.form;

    if (prevProps.raptor_connection_data !== raptor_connection_data) {
      setFieldsValue({
        id_location: raptor_connection_data.id_location,
        ip_address: raptor_connection_data.ip_address,
        db_port: raptor_connection_data.db_port,
        db_name: raptor_connection_data.db_name,
        db_username: raptor_connection_data.db_username,
        db_password: raptor_connection_data.db_password
      });
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.id_raptor_connection = this.props.raptor_connection_data.id_raptor_connection;
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { locations, loadingSubmit } = this.props.appConfig;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="updateRaptorConnection" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loadingSubmit }}
        cancelButtonProps={{ loading: loadingSubmit }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="locations.pageHeader" />}
          >
            {getFieldDecorator("id_location", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                }
              ]
            })(
              <Select
                loading={this.props.appConfig.loading}
                showSearch
                optionFilterProp="children"
                disabled={true}
              >
                {locations.map((data, key) => (
                  <Option key={key} value={data.id_location}>
                    {data.location_code + " (" + data.location_name + ")"}
                  </Option>
                ))}
              </Select>
            )}
          </Form.Item>

          <InputGroup>
            <Col span={18}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="ipAddress" />}
              >
                {getFieldDecorator("ip_address", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item {...formItemLayout} label={<IntlMessages id="port" />}>
                {getFieldDecorator("db_port", {
                  rules: []
                })(<InputNumber style={{ width: "100%" }} />)}
              </Form.Item>
            </Col>
          </InputGroup>

          <Form.Item {...formItemLayout} label={<IntlMessages id="dbName" />}>
            {getFieldDecorator("db_name", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                }
              ]
            })(<Input />)}
          </Form.Item>

          <Form.Item {...formItemLayout} label={<IntlMessages id="username" />}>
            {getFieldDecorator("db_username", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                }
              ]
            })(<Input />)}
          </Form.Item>

          <Form.Item {...formItemLayout} label={<IntlMessages id="password" />}>
            {getFieldDecorator("db_password", {})(<Input.Password />)}
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalUpdateRaptorConnection = Form.create({
  name: "modal_update_raptor_connection"
})(ModalUpdateRaptorConnection);

export default WrappedModalUpdateRaptorConnection;
